import { NavLink, useNavigate } from "react-router-dom";
import { loginState, memberIdState, memberLevelState } from "../utils/recoil";
import { useRecoilState, useRecoilValue } from "recoil";
import { useCallback } from 'react';
import axios from "axios";
import './Menu.css';
import { FaSteam } from "react-icons/fa";
import steamLogo from './steamlogo.svg';
import { MdHeight } from "react-icons/md";

const Menu = () => {
    //navigate
    const navigate = useNavigate();

    //recoil state
    const [memberId, setMemberId] = useRecoilState(memberIdState);
    const [memberLevel, setMemberLevel] = useRecoilState(memberLevelState);

    const login = useRecoilValue(loginState);//   

    //callback
    const logout = useCallback((e) => {
        //e.preventDefault();

        //recoil  memberId memberLevel 
        setMemberId("");
        setMemberLevel("");

        //axios  Authorization  
        delete axios.defaults.headers.common["Authorization"];

        //localStorage, sessionStorage refreshToken 
        window.localStorage.removeItem("refreshToken");
        window.sessionStorage.removeItem("refreshToken");

        navigate("/");
    }, [memberId, memberLevel]);

    //view
    return (<>

        <div className="row">
            <div className="col">
                <nav className="navbar  fixed-top" data-bs-theme="dark">

            <NavLink className="navbar-brand " to="/">
                    {/* <FaSteam /> */}
                    <img src={steamLogo} alt="Steam" height={"50px"} />
                </NavLink>
                </nav>
            </div>
        </div>

      
    </>);
};

export default Menu;


